"use client";

import { useState, useTransition } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { toast } from "sonner";
import { updateMinecraftUsername, deleteMinecraftUsername } from "../actions";
import { User, Edit2, Check, X, Trash2, Loader2 } from "lucide-react";

interface UsernameCardProps {
  initialUsername: string | null;
}

export function UsernameCard({ initialUsername }: UsernameCardProps) {
  const [isEditing, setIsEditing] = useState(!initialUsername);
  const [username, setUsername] = useState(initialUsername || "");
  const [isPending, startTransition] = useTransition();

  const handleSave = () => {
    if (username.trim().length < 3) {
      toast.error("Username must be at least 3 characters");
      return;
    }

    startTransition(async () => {
      try {
        await updateMinecraftUsername(username);
        toast.success("Minecraft username updated");
        setIsEditing(false);
      } catch (error) {
        toast.error(error instanceof Error ? error.message : "Failed to update username");
      }
    });
  };

  const handleDelete = () => {
      // confirm dialog could be nice here, but for now just simple action
    if (!confirm("Are you sure you want to unlink your Minecraft username?")) return;

    startTransition(async () => {
      try {
        await deleteMinecraftUsername();
        toast.success("Minecraft username removed");
        setUsername("");
        setIsEditing(true);
      } catch (error) {
        toast.error(error instanceof Error ? error.message : "Failed to remove username");
      }
    });
  };

  const handleCancel = () => {
    if (!initialUsername) {
        // If there was no username initially, we can't really "cancel" back to a viewing state of nothing,
        // so we might just clear input or do nothing.
        // But if they clicked edit on an empty state, we want to stay in edit mode or show empty state?
        // Let's reset to what it was.
    }
    setUsername(initialUsername || "");
    setIsEditing(!initialUsername);
  };

  return (
    <Card className="h-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <User className="h-5 w-5 text-primary" />
          Minecraft Profile
        </CardTitle>
        <CardDescription>
          Link your Minecraft account to access the server and sync your data.
        </CardDescription>
      </CardHeader>
      <CardContent>
        {isEditing ? (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="username">Minecraft Username</Label>
              <Input
                id="username"
                placeholder="Enter your gamertag"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                disabled={isPending}
              />
              <p className="text-xs text-muted-foreground">
                Case-sensitive. Must match exactly.
              </p>
            </div>
            <div className="flex justify-end gap-2">
              {initialUsername && (
                <Button variant="outline" size="sm" onClick={handleCancel} disabled={isPending}>
                  <X className="mr-2 h-4 w-4" /> Cancel
                </Button>
              )}
              <Button onClick={handleSave} disabled={isPending} size="sm">
                {isPending ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Saving...
                  </>
                ) : (
                  <>
                    <Check className="mr-2 h-4 w-4" /> Save Username
                  </>
                )}
              </Button>
            </div>
          </div>
        ) : (
          <div className="space-y-6">
            <div className="flex items-center justify-between rounded-lg border p-4 bg-muted/50">
              <div className="space-y-1">
                <p className="text-sm font-medium text-muted-foreground">Linked Account</p>
                <div className="flex items-center gap-2">
                  <img
                    src={`https://mc-heads.net/avatar/${initialUsername}/32`}
                    alt="Skin"
                    className="h-8 w-8 rounded"
                    onError={(e) => {
                      (e.target as HTMLImageElement).src = "https://mc-heads.net/avatar/Steve/32";
                    }}
                  />
                  <span className="font-mono text-lg font-bold">{initialUsername}</span>
                </div>
              </div>
            </div>
            
            <div className="flex gap-2">
              <Button onClick={() => setIsEditing(true)} variant="outline" className="flex-1">
                <Edit2 className="mr-2 h-4 w-4" /> Edit
              </Button>
              <Button onClick={handleDelete} variant="destructive" className="flex-1">
                <Trash2 className="mr-2 h-4 w-4" /> Unlink
              </Button>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
