"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { FileText, ArrowRight, Clock, CheckCircle2, XCircle } from "lucide-react";
import Link from "next/link";

interface ApplicationCardProps {
  status: string | null; // "pending" | "approved" | "rejected" | null
  createdAt?: Date;
  reviewedAt?: Date | null;
}

export function ApplicationCard({ status, createdAt }: ApplicationCardProps) {
  const getStatusContent = () => {
    switch (status) {
      case "pending":
        return (
          <div className="flex flex-col items-center justify-center space-y-4 py-6 text-center">
            <div className="rounded-full bg-yellow-500/10 p-4">
              <Clock className="h-8 w-8 text-yellow-500" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold">Application Under Review</h3>
              <p className="text-sm text-muted-foreground">
                Submitted on {createdAt?.toLocaleDateString()}
              </p>
            </div>
            <Badge variant="secondary" className="bg-yellow-500/10 text-yellow-500 hover:bg-yellow-500/20">
              Pending Review
            </Badge>
          </div>
        );
      case "approved":
        return (
          <div className="flex flex-col items-center justify-center space-y-4 py-6 text-center">
            <div className="rounded-full bg-green-500/10 p-4">
              <CheckCircle2 className="h-8 w-8 text-green-500" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold">Application Approved!</h3>
              <p className="text-sm text-muted-foreground">
                Welcome to the beta! You can now join the server.
              </p>
            </div>
            <Badge variant="secondary" className="bg-green-500/10 text-green-500 hover:bg-green-500/20">
              Approved
            </Badge>
          </div>
        );
      case "rejected":
        return (
          <div className="flex flex-col items-center justify-center space-y-4 py-6 text-center">
            <div className="rounded-full bg-destructive/10 p-4">
              <XCircle className="h-8 w-8 text-destructive" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold">Application Declined</h3>
              <p className="text-sm text-muted-foreground">
                Unfortunately, your application was not accepted at this time.
              </p>
            </div>
            <Badge variant="destructive">Rejected</Badge>
          </div>
        );
      default:
        return (
          <div className="flex flex-col items-center justify-center space-y-6 py-6 text-center">
            <div className="rounded-full bg-primary/10 p-4">
              <FileText className="h-8 w-8 text-primary" />
            </div>
            <div className="space-y-1">
              <h3 className="font-semibold">Join the Beta</h3>
              <p className="text-sm text-muted-foreground max-w-xs mx-auto">
                Apply for whitelist access to participate in our closed beta.
              </p>
            </div>
            <Button asChild size="lg">
              <Link href="/apply">
                Apply Now <ArrowRight className="ml-2 h-4 w-4" />
              </Link>
            </Button>
          </div>
        );
    }
  };

  return (
    <Card className="h-full">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <FileText className="h-5 w-5 text-primary" />
          Beta Application
        </CardTitle>
        <CardDescription>
          Track the status of your whitelist application.
        </CardDescription>
      </CardHeader>
      <CardContent>
        {getStatusContent()}
      </CardContent>
    </Card>
  );
}
