"use server";

import { auth, currentUser } from "@clerk/nextjs/server";
import { db } from "@/lib/db";
import { users, playerApplications } from "@/lib/db/schema";
import { eq } from "drizzle-orm";
import { revalidatePath } from "next/cache";

export async function getDashboardData() {
  const { userId } = await auth();
  if (!userId) return null;

  // Get user from DB
  let user = await db.query.users.findFirst({
    where: eq(users.clerkId, userId),
  });

  // If user doesn't exist in our DB yet but is logged in (should be handled by webhook/middleware, but for safety),
  // we could create them or just return null. For now, assuming user exists or we create on the fly if critical.
  if (!user) {
    // Attempt to sync from Clerk if missing (optional self-repair)
    const clerkUser = await currentUser();
    if (clerkUser) {
        const [newUser] = await db.insert(users).values({
            clerkId: userId,
            email: clerkUser.emailAddresses[0]?.emailAddress || "",
            minecraftUsername: null,
        }).returning();
        user = newUser;
    } else {
        return null;
    }
  }

  // Get application status
  const application = await db.query.playerApplications.findFirst({
    where: eq(playerApplications.userId, user.id),
  });

  return {
    user,
    application,
  };
}

export async function updateMinecraftUsername(username: string) {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  const cleanUsername = username.trim();
  
  // Basic validation
  if (cleanUsername.length < 3 || cleanUsername.length > 16) {
    throw new Error("Username must be between 3 and 16 characters");
  }

  if (!/^[a-zA-Z0-9_]+$/.test(cleanUsername)) {
    throw new Error("Username can only contain letters, numbers, and underscores");
  }

  // Update user
  await db.update(users)
    .set({ minecraftUsername: cleanUsername })
    .where(eq(users.clerkId, userId));

  revalidatePath("/dashboard");
  return { success: true };
}

export async function deleteMinecraftUsername() {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  await db.update(users)
    .set({ minecraftUsername: null })
    .where(eq(users.clerkId, userId));

  revalidatePath("/dashboard");
  return { success: true };
}
